
function Config({ WordLength, setWordLength, setIsUnique, isUnique, HandleSubmitConfig }) {

    function HandleSubmit(e) {
        e.preventDefault();
        HandleSubmitConfig(isUnique, WordLength);
    }
    return (
        <>
            <div>
                <p>Vlj hur mnga bokstver och om orden ska ha unika eller tillta samma bokstav flera gnger</p>
            </div>
            <form onSubmit={HandleSubmit}>
            
                <label>Vlj hur mnga bokstver du vill spela med: </label>
                <select
                    value={WordLength}
                    onChange={(e) => setWordLength(parseInt(e.target.value))}>
                    <option value="4">4</option>
                    <option value="5">5</option>
                    <option value="6">6</option>                  
                </select>
                
                <br />
                <br />
                <div>
                    <input
                        type="radio"
                        value={isUnique}
                        onChange={() => setIsUnique(false)}
                        checked={!isUnique}
                    />
                    Tillt samma bokstver flera gnger.
                    <br />
                    <input
                        type="radio"
                        value={isUnique}
                        onChange={() => setIsUnique(true)}
                        checked={isUnique}
                    />
                    Endast unika bokstver.
                </div>
                <button type="submit">Starta spel!</button>
            </form>
        </>
    );
}

export default Config;